<template>
  <div class="w-full md:w-1/2">
    <div
      class="leading-loose max-w-xl m-4 p-7 bg-secondary-light dark:bg-secondary-dark rounded-xl shadow-xl text-left"
    >
      <p
        class="font-general-medium text-primary-dark dark:text-primary-light text-2xl mb-8"
      >
        Formulario de contacto
      </p>
      <form class="font-general-regular space-y-7" @submit.prevent="contacto">
        <div>
          <label
            class="block text-lg text-primary-dark dark:text-primary-light mb-2"
            for="name"
            >Nombre completo</label
          >
          <input
            class="w-full px-5 py-2 border border-gray-300 dark:border-primary-dark border-opacity-50 text-primary-dark dark:text-secondary-light bg-ternary-light dark:bg-ternary-dark rounded-md shadow-sm text-md"
            id="name"
            type="text"
            placeholder="Tu nombre"
            aria-label="Name"
            v-model="formContacto.nombre"
            :class="{ error: formError.nombre }"
          />
        </div>
        <div>
          <label
            class="block text-lg text-primary-dark dark:text-primary-light mb-2"
            for="email"
            >Correo electrónico</label
          >
          <input
            class="w-full px-5 py-2 border border-gray-300 dark:border-primary-dark border-opacity-50 text-primary-dark dark:text-secondary-light bg-ternary-light dark:bg-ternary-dark rounded-md shadow-sm text-md"
            id="email"
            type="email"
            placeholder="Tu correo electrónico"
            aria-label="Email"
            v-model="formContacto.email"
            :class="{ error: formError.email }"
          />
        </div>
        <div>
          <label
            class="block text-lg text-primary-dark dark:text-primary-light mb-2"
            for="subject"
            >Asunto</label
          >
          <input
            class="w-full px-5 py-2 border border-gray-300 dark:border-primary-dark border-opacity-50 text-primary-dark dark:text-secondary-light bg-ternary-light dark:bg-ternary-dark rounded-md shadow-sm text-md"
            id="subject"
            type="text"
            placeholder="Asunto"
            aria-label="Subject"
            v-model="formContacto.asunto"
            :class="{ error: formError.asunto }"
          />
        </div>

        <div>
          <label
            class="block text-lg text-primary-dark dark:text-primary-light mb-2"
            for="message"
            >Mensaje</label
          >
          <textarea
            class="w-full px-5 py-2 border border-gray-300 dark:border-primary-dark border-opacity-50 text-primary-dark dark:text-secondary-light bg-ternary-light dark:bg-ternary-dark rounded-md shadow-sm text-md"
            id="message"
            cols="14"
            rows="6"
            aria-label="Message"
            v-model="formContacto.mensaje"
            :class="{ error: formError.mensaje }"
          ></textarea>
        </div>
        <div>
          <Button
            title="Enviar mensaje"
            class="px-4 py-2.5 text-white tracking-wider bg-indigo-500 hover:bg-indigo-600 focus:ring-1 focus:ring-indigo-900 rounded-lg duration-500"
            type="submit"
            aria-label="Send Message"
          />
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { ref, inject } from "vue";
import * as Yup from "yup";
import Button from "../reusable/Button.vue";
import { ContactoApi } from "../../api/contacto";

export default {
  components: { Button },
  setup() {
    let formContacto = ref({});
    let formError = ref({});
    const toast = inject("WKToast");

    const schemaForm = Yup.object().shape({
      nombre: Yup.string().required(true),
      email: Yup.string().email(true).required(true),
      asunto: Yup.string().required(true),
      mensaje: Yup.string().required(true),
    });

    const contacto = async () => {
      formError.value = {};

      var form = {
        data: formContacto.value,
      };

      try {
        await schemaForm.validate(formContacto.value, { abortEarly: false });
        try {
          const resp = await ContactoApi(form);
          console.log(resp);
          toast("¡Mensaje enviado!", {
            className: ["my-toast"],
            horizontalPosition: "left",
            verticalPosition: "bottom",
            duration: 3000,
            transition: "transition",
          });

          formContacto.value = {};
        } catch (error) {
          console.log(error);
        }
      } catch (error) {
        error.inner.forEach((err) => {
          formError.value[err.path] = err.message;
        });
      }
    };

    return {
      formContacto,
      formError,
      contacto,
    };
  },
};
</script>

<style scoped>
input.error {
  background: rgb(243, 201, 201);
  border: 2px solid rgb(241, 125, 125);
  color: white !important;
}
textarea.error {
  background: rgb(243, 201, 201);
  border: 2px solid rgb(241, 125, 125);
  color: white !important;
}
</style>
